.footer {
  border: 1px solid $color-white;
  background-color: #78aaf5;
  grid-area: footer;
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr;
  align-items: center;
  justify-content: center;

  background: $color-black;

  &__container {
    display: flex;
    justify-content: center;
    align-items: center;
    background: $color-black;
  }
}

